<template>
    <div>
        <!-- page -->
        <el-container width="200px">
            <el-header style="text-align: center; vertical-align:middle;font-size: 40px;height:100px">
                <el-row></el-row>
                <span>{{systemname}}</span>

            </el-header>
            <el-main style="text-align: center; background-color:#000000">
                <el-row>
                    <el-radio-group v-model="method">
                        <el-radio :label="0"></el-radio>
                        <el-radio :label="1"></el-radio>
                    </el-radio-group>
                </el-row>
                <el-row>
                    <el-button name="calc" type="primary" @click.native="calcClick"></el-button>
                </el-row>
            </el-main>
            <el-main style="text-align: center">
                <el-tabs v-model="activeName" @tab-click.native="handleClick">
                    <el-tab-pane label="" name="first">
                        <el-divider content-position="left"></el-divider>
                        <el-input type="textarea" :rows="5" placeholder="" v-model="aim"></el-input>
                        <el-divider content-position="left"></el-divider>
                        <el-input type="textarea" :rows="5" placeholder="" v-model="range"></el-input>
                        <el-divider content-position="left"></el-divider>
                        <el-input type="textarea" :rows="5" placeholder="" v-model="team"></el-input>
                    </el-tab-pane>
                    <el-tab-pane label="" name="second">
                        <div style="float:left">
                            <el-button type="add" @click.native="assetDialogVisible = true"></el-button>
                        </div>
                        <el-table :data="tableAsset" border :header-cell-style="{background:'#FFFFFF'}" :cell-style="{background:'#FFFFFF'}">
                            <el-table-column prop="asset" label="" width="260">
                            </el-table-column>
                            <el-table-column prop="integrity" label="" width="140">
                            </el-table-column>
                            <el-table-column prop="availability" label="" width="140">
                            </el-table-column>
                            <el-table-column prop="confidentiality" label="" width="140">
                            </el-table-column>
                            <el-table-column prop="rank" label="" width="140">
                            </el-table-column>
                            <el-table-column prop="details" label="" >
                            </el-table-column>
                            <el-table-column fixed="right" label="" width="180">
                                <template slot-scope="scope">
                                    <el-button @click.native="formAssetModify(scope.$index)" type="text" size="small" style="font-size:16px;width:38px;height:20px"></el-button>
                                    <el-button @click.native="formDelete(scope.$index, tableAsset)" type="text" size="small" style="font-size:16px;width:38px;height:20px"></el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-tab-pane>
                    <el-tab-pane label="" name="third">
                        <div style="float:left">
                            <el-button type="add" @click.native="vulDialogVisible = true"></el-button>
                        </div>
                        <el-table :data="tableVul" border :header-cell-style="{background:'#FFFFFF'}" :cell-style="{background:'#FFFFFF'}">
                            <el-table-column prop="vulnerability" label="" width="260"></el-table-column>
                            <el-table-column prop="rank" label="" width="140"></el-table-column>
                            <el-table-column prop="vaString" label="" width="140"></el-table-column>
                            <el-table-column prop="details" label=""></el-table-column>
                            <el-table-column fixed="right" label="" width="180">
                                <template slot-scope="scope">
                                    <el-button @click.native="formVulModify(scope.$index)" type="text" size="small" style="font-size:16px;width:38px;height:20px"></el-button>
                                    <el-button @click.native="formDelete(scope.$index, tableVul)" type="text" size="small" style="font-size:16px;width:38px;height:20px"></el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-tab-pane>
                    <el-tab-pane label="" name="fourth">
                        <div style="float:left">
                            <el-button type="add" @click.native="threatDialogVisible = true"></el-button>
                        </div>
                        <el-table :data="tableThreat" border :header-cell-style="{background:'#FFFFFF'}" :cell-style="{background:'#FFFFFF'}">
                            <el-table-column prop="threat" label="" width="260"></el-table-column>
                            <el-table-column prop="rank" label="" width="140"></el-table-column>
                            <el-table-column prop="tvString" label="" width="140"></el-table-column>
                            <el-table-column prop="details" label=""></el-table-column>
                        </el-table>
                    </el-tab-pane>
                </el-tabs>
            </el-main>
        </el-container>

        <!-- dialogs -->
        <el-dialog title="" :visible.sync="assetDialogVisible" :modal-append-to-body="false" :show-close="false">
            <el-form :model="formAsset">
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="formAsset.name" autocomplete="off" width="200px"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="formAsset.confidentiality" placeholder="">
                        <el-option label="" value=5></el-option>
                        <el-option label="" value=4></el-option>
                        <el-option label="" value=3></el-option>
                        <el-option label="" value=2></el-option>
                        <el-option label="" value=1></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="formAsset.integrity" placeholder="">
                        <el-option label="" value=5></el-option>
                        <el-option label="" value=4></el-option>
                        <el-option label="" value=3></el-option>
                        <el-option label="" value=2></el-option>
                        <el-option label="" value=1></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="formAsset.availability" placeholder="">
                        <el-option label="" value=5></el-option>
                        <el-option label="" value=4></el-option>
                        <el-option label="" value=3></el-option>
                        <el-option label="" value=2></el-option>
                        <el-option label="" value=1></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-input type="textarea" :rows="5" placeholder="" v-model="formAsset.details"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="dialogAssetClose"> </el-button>
                <el-button type="primary" @click.native="formAssertClick"> </el-button>
            </div>
        </el-dialog>

        <el-dialog title="" :visible.sync="vulDialogVisible" :modal-append-to-body="false" :show-close="false">
            <el-form :model="formVul">
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="formVul.name" autocomplete="off" width="200px"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="formVul.rank" placeholder="">
                        <el-option label="" value=5></el-option>
                        <el-option label="" value=4></el-option>
                        <el-option label="" value=3></el-option>
                        <el-option label="" value=2></el-option>
                        <el-option label="" value=1></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-checkbox-group v-model="formVul.va">
                        <el-checkbox v-for="i in tableAsset" :label="i.asset" :key="i.asset">{{i.asset}}</el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item>
                    <el-input type="textarea" :rows="5" placeholder="" v-model="formVul.details"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="dialogVulClose"> </el-button>
                <el-button type="primary" @click.native="formVulClick"> </el-button>
            </div>
        </el-dialog>

        <el-dialog title="" :visible.sync="threatDialogVisible" :modal-append-to-body="false" :show-close="false">
            <el-form :model="formThreat">
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="formThreat.name" autocomplete="off" width="200px"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="formThreat.rank" placeholder="">
                        <el-option label="" value=5></el-option>
                        <el-option label="" value=4></el-option>
                        <el-option label="" value=3></el-option>
                        <el-option label="" value=2></el-option>
                        <el-option label="" value=1></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-checkbox-group v-model="formThreat.tv">
                        <el-checkbox v-for="i in tableVul" :label="i.vulnerability" :key="i.vulnerability">{{i.vulnerability}}</el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item>
                    <el-input type="textarea" :rows="5" placeholder="" v-model="formThreat.details"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="dialogThreatClose"> </el-button>
                <el-button type="primary" @click.native="formThreatClick"> </el-button>
            </div>
        </el-dialog>

        <el-dialog title="" :visible.sync="resultDialogVisible" :modal-append-to-body="false">
            <div slot="title" class="el-dialog__header">
                <h2></h2>
                <el-button type="primary" @click.native="createWord"></el-button>
                <el-button type="primary" @click.native="createHist"></el-button>
            </div>
            <el-table :data="tva_results" border :header-cell-style="{background:'#FFFFFF'}" :cell-style="{background:'#FFFFFF'}">
                <el-table-column prop="asset" label="" width="160"></el-table-column>
                <el-table-column prop="threat" label="" width="160"></el-table-column>
                <el-table-column prop="vulnerability" label="" width="160"></el-table-column>
                <el-table-column prop="level" label=""></el-table-column>
            </el-table>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="resultDialogVisible = false"> </el-button>
                <el-button type="primary" @click.native="submitClick"> </el-button>
            </div>
        </el-dialog>

        <el-dialog :visible.sync="chartDialogVisible" :modal-append-to-body="false" width="60%">
            <div id="chart" style="width: 1200px;height:400px;"></div>
        </el-dialog>

    </div>
</template>

<script>
    // Matrix method:
    // F[a][v] === level
    const F = [
        [1, 1, 2, 2, 3],
        [1, 1, 2, 3, 4],
        [1, 2, 3, 3, 4],
        [1, 2, 3, 3, 5],
        [2, 2, 4, 5, 5]
    ];
    // L[t][v] === level
    const L = [
        [1, 1, 2, 2, 3],
        [1, 2, 2, 3, 4],
        [1, 2, 3, 3, 4],
        [2, 2, 3, 4, 5],
        [2, 3, 4, 4, 5]
    ];
    // R[f][l] === level
    const R = [
        [1, 1, 2, 2, 3],
        [1, 2, 2, 3, 3],
        [1, 2, 3, 3, 4],
        [2, 2, 3, 4, 4],
        [2, 3, 4, 4, 5]
    ];

    const map = ['', '', '', '', ''];
    const methods = ['', ''];

    export default {
        name: '_new',
        data() {
            return {
                // Page settings
                activeName: 'first',
                formLabelWidth: '120px',
                assetDialogVisible: false,
                threatDialogVisible: false,
                vulDialogVisible: false,
                resultDialogVisible: false,
                chartDialogVisible: false,

                // Tab data
                aim: '',
                range: '',
                team: '',
                tableAsset: [],
                tableVul: [],
                tableThreat: [],

                // Form data
                formAsset: {
                    name: '',
                    availability: null,
                    integrity: null,
                    confidentiality: null,
                    details: ''
                },
                formThreat: {
                    name: '',
                    rank: null,
                    tv: [],
                    details: ''
                },
                formVul: {
                    name: '',
                    rank: null,
                    va: [],
                    details: ''
                },

                // System data
                systemname: 'test1',
                method: 0,
                tva_results: []

            }
        },

        created () {
            this.systemname = this.$route.query.sysname;
            this.$http({
                method: 'GET',
                // url: 'http://134.175.225.180:3000/mock/43/sys/query',
                url: '/sys/query',
                params: {
                    systemname: this.systemname
                }
            }).then(
                (response) => {
                    // success
                    if (response.ok && response.body.status === 0) {
                        this.loadData(response.body);
                    } else {
                        if(response.body.error !== '')
                            this.messageBox('', response.body.error);
                    }
                },
                (error) => {
                    // error?
                    this.messageBox('', error.body.error);
                }
            );
        },

        methods: {
            loadData(data) {
                this.method = methods.indexOf(data.method);
                this.tva_results = data.results.tva_results;

                // Parse assets
                for(let i of data.assets)
                {
                    let cia = i.description.split('&&&');
                    let temp = {asset: i.assetname, confidentiality: cia[1], integrity: cia[2],
                        availability: cia[3], details: cia[0], rank: map[i.val - 1]};
                    this.tableAsset.push(temp);
                }

                // Parse vulnerabilities
                for(let i of data.vulnerabilities)
                {
                    let va = data.results.va_results[i.vulname];
                    let temp = {vulnerability: i.vulname, rank: map[i.val - 1],
                        details: i.description, va: va, vaString: va.toString()};
                    this.tableVul.push(temp);
                }

                // Parse threats
                for(let i of data.threats)
                {
                    let tv = data.results.tv_results[i.threatname];
                    let temp = {threat: i.threatname, rank: map[i.val - 1],
                        details: i.description, tv: tv, tvString: tv.toString()};
                    this.tableThreat.push(temp);
                }

                // Parse description
                let str = data.description;
                let temp = str.indexOf('&&&');
                this.aim = str.substr(3, temp - 3);
                str = str.substr(temp + 6, str.length - temp - 6);
                temp = str.indexOf('&&&');
                this.range = str.substr(0, temp);
                str = str.substr(temp + 6, str.length - temp - 6);
                temp = str.indexOf('&&&');
                this.team = str.substr(0, temp);

            },

            levelMultiply(val) {
                // Can't have any malicious values at this point
                return Math.ceil((val - 1) / 5);
            },

            messageBox(title, text) {
                this.$alert(text, title, {
                    confirmButtonText: ''
                });
            },

            handleClick(tab, event) {
                // console.log(tab, event);
            },

            dialogAssetClose() {
                this.formAsset = {
                    name: '',
                    availability: null,
                    integrity: null,
                    confidentiality: null,
                    details: ''
                };
                this.assetDialogVisible = false;
            },

            dialogVulClose() {
                this.formVul = {
                    name: '',
                    rank: null,
                    va: [],
                    details: ''
                };
                this.vulDialogVisible = false;
            },

            dialogThreatClose() {
                this.formThreat = {
                    name: '',
                    rank: null,
                    tv: [],
                    details: ''
                };
                this.threatDialogVisible = false;
            },

            formAssertClick() {
                if(this.formAsset.name === '' || this.formAsset.confidentiality === null
                    || this.formAsset.integrity === null || this.formAsset.availability === null)
                    this.messageBox('', '');
                else {
                    // Calculate val
                    let val = this.formAsset.availability;
                    if (this.formAsset.integrity > val)
                        val = this.formAsset.integrity;
                    if (this.formAsset.confidentiality > val)
                        val = this.formAsset.confidentiality;

                    // Move data
                    let temp = {
                        asset: this.formAsset.name,
                        confidentiality: map[this.formAsset.confidentiality - 1],
                        integrity: map[this.formAsset.integrity - 1],
                        availability: map[this.formAsset.availability - 1],
                        details: this.formAsset.details,
                        rank: map[val - 1]
                    };

                    // Check&Delete
                    for(let i = 0; i < this.tableAsset.length; ++i)
                        if(this.tableAsset[i].asset === temp.asset)
                            this.tableAsset.splice(i, 1);

                    this.tableAsset.push(temp);

                    // Close dialog
                    this.dialogAssetClose();
                }
            },

            formThreatClick() {
                if(this.formThreat.name === '' || this.formThreat.rank === null)
                    this.messageBox('', '');
                else
                {
                    // Move data
                    let temp = {
                        threat: this.formThreat.name,
                        tv: this.formThreat.tv,
                        tvString: this.formThreat.tv.toString(),
                        details: this.formThreat.details,
                        rank: map[this.formThreat.rank - 1]
                    };

                    // Check&Delete
                    for(let i = 0; i < this.tableThreat.length; ++i)
                        if(this.tableThreat[i].threat === temp.threat)
                            this.tableThreat.splice(i, 1);

                    this.tableThreat.push(temp);

                    // Close dialog
                    this.dialogThreatClose();
                }
            },

            formVulClick() {
                if(this.formVul.name === '' || this.formVul.rank === null)
                    this.messageBox('', '');
                else
                {
                    // Move data
                    let temp = {
                        vulnerability: this.formVul.name,
                        va: this.formVul.va,
                        vaString: this.formVul.va.toString(),
                        details: this.formVul.details,
                        rank: map[this.formVul.rank - 1]
                    };

                    // Check&Delete
                    for(let i = 0; i < this.tableVul.length; ++i)
                        if(this.tableVul[i].vulnerability === temp.vulnerability)
                            this.tableVul.splice(i, 1);

                    this.tableVul.push(temp);

                    // Close dialog
                    this.dialogVulClose();
                }
            },

            formAssetModify(index) {
                // Fill data
                this.formAsset.name = this.tableAsset[index].asset;
                this.formAsset.details = this.tableAsset[index].details;
                this.formAsset.confidentiality = String(map.indexOf(this.tableAsset[index].confidentiality) + 1);
                this.formAsset.integrity = String(map.indexOf(this.tableAsset[index].integrity) + 1);
                this.formAsset.availability = String(map.indexOf(this.tableAsset[index].availability) + 1);

                // Open the same dialog as ADD
                this.assetDialogVisible = true;

            },

            formVulModify(index) {
                // Fill data
                this.formVul.name = this.tableVul[index].vulnerability;
                this.formVul.details = this.tableVul[index].details;
                this.formVul.rank = String(map.indexOf(this.tableVul[index].rank) + 1);
                this.formVul.va = this.tableVul[index].va;

                // Open the same dialog as ADD
                this.vulDialogVisible = true;
            },

            formThreatModify(index) {
                // Fill data
                this.formThreat.name = this.tableThreat[index].threat;
                this.formThreat.details = this.tableThreat[index].details;
                this.formThreat.rank = String(map.indexOf(this.tableThreat[index].rank) + 1);
                this.formThreat.tv = this.tableThreat[index].tv;

                // Open the same dialog as ADD
                this.threatDialogVisible = true;
            },

            formDelete(index, rows) {
                rows.splice(index, 1);
            },

            calcClick() {
                // Calculate results
                // Expand from v
                this.tva_results = [];
                for(let v of this.tableVul)
                {
                    // Search t
                    for(let t of this.tableThreat)
                    {
                        let flag = false;
                        for(let i of t.tv)
                            if(i === v.vulnerability)
                            {
                                flag = true;
                                break;
                            }

                        if(flag === true)
                        {
                            // Search a
                            for(let a of this.tableAsset)
                            {
                                flag = false;
                                for(let i of v.va)
                                    if(i === a.asset)
                                    {
                                        flag = true;
                                        break;
                                    }

                                if(flag === true)
                                {
                                    // append
                                    let temp = {asset: a.asset, vulnerability: v.vulnerability, threat: t.threat,
                                        level: 0};
                                    if(this.method === 0)  // Multiply
                                    {
                                        let val = Math.sqrt((map.indexOf(a.rank) + 1) * (map.indexOf(v.rank) + 1)) *
                                            Math.sqrt((map.indexOf(v.rank) + 1) * (map.indexOf(t.rank) + 1));
                                        temp.level = this.levelMultiply(val);
                                    }
                                    else  // Matrix
                                        temp.level = R[F[map.indexOf(a.rank)][map.indexOf(v.rank)] - 1][L[map.indexOf(t.rank)][map.indexOf(v.rank)] - 1];

                                    this.tva_results.push(temp);
                                }
                            }
                        }
                    }
                }

                // Show results
                this.resultDialogVisible = true;
            },

            submitClick() {
                // Submit a system to server
                let tempMethod = '';
                if(this.method === 1)
                    tempMethod = '';

                let tempAsset = [];
                let tempThreat = [];
                let tempVul = [];
                let tempResults = {tva_results: this.tva_results, tv_results: {}, va_results: {}};
                for(let i of this.tableAsset)
                {
                    let temp = {assetname: i.asset, val: map.indexOf(i.rank) + 1,
                        description: i.details + '&&&' + i.confidentiality + '&&&' + i.integrity + '&&&' + i.availability + '&&&'};
                    tempAsset.push(temp);
                }
                for(let i of this.tableThreat)
                {
                    tempResults.tv_results[i.threat] = i.tv;
                    let temp = {threatname: i.threat, val: map.indexOf(i.rank) + 1, description: i.details};
                    tempThreat.push(temp);
                }
                for(let i of this.tableVul)
                {
                    tempResults.va_results[i.vulnerability] = i.va;
                    let temp = {vulname: i.vulnerability, val: map.indexOf(i.rank) + 1, description: i.details};
                    tempVul.push(temp);
                }
                this.$http({
                    method: 'POST',
                    //url: 'http://134.175.225.180:3000/mock/43/sys/submit',
                    url: '/sys/submit',
                    emulateJSON: true,
                    body: JSON.stringify({
                        systemname: this.systemname,
                        description: '' + this.aim + '&&&' + this.range + '&&&' + this.team + '&&&',
                        method: tempMethod,
                        assets: tempAsset,
                        threats: tempThreat,
                        vulnerabilities: tempVul,
                        results: tempResults
                    })
                }).then(
                    (response) => {
                        // success
                        if (response.ok && response.body.status === 0) {
                            this.messageBox('', '');
                        } else {
                            this.messageBox('', response.body.error);
                        }
                    },
                    (error) => {
                        // error?
                        this.messageBox('', error.body.error);
                    }
                );
                this.resultDialogVisible = false;
            },

            // Create a table in .docx
            createTable(doc, title, data, tableMap, tableMapCh, tableParaSize) {
                const titleResults = new Paragraph(title).title();
                const tableResults = new Table(data.length + 1, tableMap.length);
                tableResults.setWidth(WidthType.PERCENTAGE, '100%');
                // headers
                for(let i = 0; i < tableMapCh.length; ++i)
                    tableResults.getCell(0, i)
                        .addContent(new Paragraph().center().addRun((new TextRun(tableMapCh[i]).size(tableParaSize))));
                // data
                for(let i = 0; i < data.length; ++i)
                    for(let j = 0; j < tableMap.length; ++j)
                    {
                        const temp = new Paragraph();
                        temp.addRun(new TextRun(data[i][tableMap[j]]).size(tableParaSize));
                        const cell = tableResults.getCell(i + 1, j);
                        cell.addContent(temp);
                    }

                doc.addParagraph(titleResults);
                doc.addParagraph(tableResults);
            },

            createWord() {
                // Definitions
                const spacing = new Paragraph();
                const tvaMap = ['asset', 'vulnerability', 'threat', 'level'];
                const tvaMapCh = ['', '', '', ''];
                const assetMap = ['asset', 'confidentiality', 'integrity', 'availability', 'rank', 'details'];
                const assetMapCh = ['', '', '', '', '', ''];
                const vulMap = ['vulnerability', 'vaString', 'rank', 'details'];
                const vulMapCh = ['', '', '', ''];
                const threatMap = ['threat', 'tvString', 'rank', 'details'];
                const threatMapCh = ['', '', '', ''];
                const tableParaSize = 28;
                const paraSize = 32;

                // Descriptions
                const paragraphName = new Paragraph().addRun(new TextRun('' + this.systemname).size(paraSize));
                const paragraphMethod = new Paragraph().addRun(new TextRun('' + methods[this.method]).size(paraSize));
                const paragraphAim = new Paragraph().addRun(new TextRun('' + this.aim).size(paraSize));
                const paragraphRange = new Paragraph().addRun(new TextRun('' + this.range).size(paraSize));
                const paragraphTeam = new Paragraph().addRun(new TextRun('' + this.team).size(paraSize));

                // Construct document
                const doc = new Document();
                doc.addParagraph(paragraphName);
                doc.addParagraph(paragraphMethod);
                doc.addParagraph(paragraphAim);
                doc.addParagraph(paragraphRange);
                doc.addParagraph(paragraphTeam);
                doc.addParagraph(spacing);
                this.createTable(doc, '', this.tableAsset, assetMap, assetMapCh, tableParaSize);
                this.createTable(doc, '', this.tableVul, vulMap, vulMapCh, tableParaSize);
                this.createTable(doc, '', this.tableThreat, threatMap, threatMapCh, tableParaSize);
                doc.addParagraph(spacing);
                this.createTable(doc, '', this.tva_results, tvaMap, tvaMapCh, tableParaSize);

                // Export
                const packer = new Packer();

                packer.toBlob(doc).then(blob => {
                    saveAs(blob, this.systemname + '.docx');
                });
            },

            createHist()
            {
                this.chartDialogVisible = true;
                this.$nextTick(function () {
                    // Imports
                    let echarts = require('echarts/lib/echarts');
                    require('echarts/lib/chart/bar');
                    require('echarts/lib/component/tooltip');
                    require('echarts/lib/component/title');

                    // Find DOM
                    let myChart = echarts.init(document.getElementById('chart'));

                    // Define histogram
                    let histOptions = {
                        title: {
                            text: ''
                        },
                        tooltip: {},
                        xAxis: {
                            data: []
                        },
                        yAxis: {},
                        axisLabel: {
                            interval:0,
                            rotate:40
                        },
                        series: [{
                            type: 'bar',
                            data: []
                        }]
                    };

                    // Construct bars & Calculate indices
                    let levels = [];
                    let assets = [];
                    let indices = [];
                    let xAxis = [];
                    let total = 0;
                    for(let i of this.tableAsset)
                    {
                        assets.push(i.asset);
                        let count = 0;
                        for(let j of this.tva_results)
                            if(j.asset === i.asset)
                            {
                                levels.push(j.level);
                                xAxis.push('');
                                ++count;
                            }

                        ++count;
                        levels.push(0);
                        xAxis.push('');
                        indices.push(Math.floor(count / 2) + total - 1);
                        total += count;
                    }

                    // Rearrange xAxis
                    for(let i = 0; i < assets.length; ++i)
                        xAxis[indices[i]] = assets[i];

                    // Draw
                    histOptions.xAxis.data = xAxis;
                    histOptions.series[0].data = levels;
                    myChart.setOption(histOptions);

                });
            }

        }
    };
</script>


