<template>
    <div class="top-align">
        <h1></h1>
        <el-row>
            <el-col :span="10"><div class="grid-content"></div></el-col>
            <el-col :span="4">
                <div class="bg-light">
                    <el-row><div class="grid-content bg-dark"></div></el-row>
                    <el-form :label-position="labelPosition">
                        <el-form-item>
                            <el-input name="username" prefix-icon="el-icon-arrow-right" size="medium" v-model="username" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-input name="password" prefix-icon="el-icon-arrow-right" size="medium" v-model="password" type="password" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button name="login" type="primary" round @click.native=login_click></el-button>
                            <el-button name="register" round plain @click.native=register_click></el-button>
                            <p class="error">{{errorMessage}}</p>
                        </el-form-item>
                    </el-form>
                </div>
            </el-col>
            <el-col :span="10"><div class="grid-content"></div></el-col>
        </el-row>
    </div>
</template>


<script>
    export default {
        name: 'login',
        data(){
            return{
                labelPosition: 'right',
                username: '',
                password: '',
                errorMessage: ''

            }
        },

        methods:{
            dump: function() {
                // self-explainable
                this.username = '';
                this.password = '';
            },

            register_click: function() {
                this.$http({
                    method: 'POST',
                    //url: 'http://134.175.225.180:3000/mock/43/auth/register',
                    url: '/auth/register',
                    emulateJSON: true,
                    body: JSON.stringify({
                        username: this.username,
                        password: this.password
                    })
                }).then(
                    (response) => {
                        // success
                        if (response.ok && response.body.status === 0) {
                            this.errorMessage = '';
                        } else {
                            this.errorMessage = response.body.error;
                        }
                    },
                    (error) => {
                        // error?
                        this.errorMessage = error.body.error;
                    }
                );
            },

            login_click: function() {
                this.$http({
                    method: 'POST',
                    // url: 'http://134.175.225.180:3000/mock/43/auth/login',
                    url: '/auth/login',
                    emulateJSON: true,
                    body: JSON.stringify({
                        username: this.username,
                        password: this.password
                    })
                }).then(
                    (response) => {
                        // success
                        if (response.ok && response.body.status === 0) {
                            this.$router.push({path: '/mainPage'});
                        } else {
                            this.errorMessage = response.body.error;
                        }
                    },
                    (error) => {
                        // error?
                        this.errorMessage = error.body.error;
                    }
                );

            }
        }
    }


</script>

<style scoped>
    .el-row {
        margin-bottom: 10px;
    }
    .el-col {
        border-radius: 4px;
    }
    .el-input {
        width: 200px;
    }
    .el-button {
        margin: 20px;
    }
    .bg-dark {
        background: #000000;
    }
    .bg-light {
        background: #c0c1cc;
    }
    .grid-content {
        border-radius: 4px;
        min-height: 36px;
    }
    .top-align {
        margin-top: 10%;
    }
    .error {
        color: red;
    }

</style>